import React, { useEffect, useState } from "react";
import Header from "../common/headerCommon";
import { useDispatch, useSelector } from "react-redux";
import DescriptionPost from "./post";
import { Col, Row } from "react-grid-system";
import styled from "./style.module.css";
import { GetDec } from "../../redux/store/description";
import DeleteDescription from "./delete";
import DescriptionPut from "./put";

function DescriptionComponent() {
  const [modal, setModal] = useState(false);

  const [delet, setDelete] = useState(false);

  const [put, setPut] = useState(false);

  const [id, setId] = useState();
  const [put_id, setPut_Id] = useState();

  const dispatch = useDispatch();
  const HandelOpen = () => {
    setModal(true);
  };
  const HandelClose = () => {
    setModal(false);
  };
  const HandelOpenDel = () => {
    setDelete(true);
  };
  const HandelCloseDel = () => {
    setDelete(false);
  };
  const HandelOpenPut = () => {
    setPut(true);
  };
  const HandelClosePut = () => {
    setPut(false);
  };
  useEffect(() => {
    dispatch(GetDec());
  }, []);
  const Data = useSelector((state) => state.dec.getDec.Data);
  return (
    <>
      <Header title={"Description"} handelOpen={HandelOpen} />
      <DescriptionPost modal={modal} handleClose={HandelClose} />
      <div className={styled.Wrapper}>
        <Row>
          {Data.map((elem, index) => (
            <Col xs={3} sm={3}>
              <div className={styled.card}>
                <img src={elem.img} alt="photo" />
                <div className={styled.texts}>
                  <h2>{elem.title_uz}</h2>
                  <h3>{elem.category_uz}</h3>
                  <h3>{elem.day}</h3>
                  <h3>{elem.category_month}</h3>
                  <p>{elem.description_uz}</p>
                  <p>{elem.date}</p>
                </div>
              </div>
              <div className={styled.buttons}>
                <button
                  onClick={() => {
                    setId(elem.id);
                    HandelOpenDel();
                  }}
                  className={styled.delete}
                >
                  Delete
                </button>
                <button
                  onClick={() => {
                    setPut_Id(elem.id);
                    HandelOpenPut();
                  }}
                  className={styled.put}
                >
                  Edit
                </button>
              </div>
            </Col>
          ))}
        </Row>
      </div>
      <DeleteDescription modal={delet} handleClose={HandelCloseDel} id={id} />
      <DescriptionPut modal={put} handleClose={HandelClosePut} id={put_id} />
    </>
  );
}

export default DescriptionComponent;
