import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import styled from "./style.module.css";
import { useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { GetNews, UploadImage } from "../../../redux/store/news";
import { PutNews } from "../../../redux/store/news";

function NewsPut({ modal, handleClose, id }) {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const dispatch = useDispatch();
  const inputFileRef = useRef(null);
  const onBtnClick = (e) => {
    e.preventDefault();
    inputFileRef.current.click();
  };
  const onFilechange = (e) => {
    dispatch(UploadImage(e));
  };
  const Loading = useSelector((state) => state.news.uploadNews.Loading);
  const DataImg = useSelector((state) => state.news.uploadNews.data);
  const catUz = useRef();
  const catRU = useRef();
  const catEn = useRef();
  const titUz = useRef();
  const titRu = useRef();
  const titEn = useRef();
  const decUz = useRef();
  const decRu = useRef();
  const decEn = useRef();
  const date = useRef();
  const Put_id = id;

  const HandePut = async (e) => {
    e.preventDefault();
    const body = {
      category_uz: catUz.current.value,
      category_ru: catRU.current.value,
      category_en: catEn.current.value,
      title_uz: titUz.current.value,
      title_ru: titRu.current.value,
      title_en: titEn.current.value,
      description_uz: decUz.current.value,
      description_ru: decRu.current.value,
      description_en: decEn.current.value,
      date: date.current.value,
      img: DataImg,
    };
    await dispatch(PutNews({ body, id: Put_id }));
    dispatch(GetNews());
    handleClose();
  };

  const Data = useSelector((state) => state.news.getNews.Data);
  return (
    <>
      <div>
        <Modal
          open={modal}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            {Data.map((elem, index) =>
              elem.id == id ? (
                <>
                  <div className={styled.Wrapper} key={index}>
                    <form className={styled.form} onSubmit={HandePut}>
                      <input
                        type="file"
                        style={{ display: "none" }}
                        ref={inputFileRef}
                        onChange={onFilechange}
                      />
                      <button onClick={onBtnClick}>
                        {Loading ? <>Loading...</> : <>Edit img </>}
                      </button>
                      <input
                        ref={catUz}
                        type="text"
                        placeholder={elem.category_uz}
                      />
                      <input
                        ref={catRU}
                        type="text"
                        placeholder={elem.category_ru}
                      />
                      <input
                        ref={catEn}
                        type="text"
                        placeholder={elem.category_en}
                      />
                      <input
                        ref={titUz}
                        type="text"
                        placeholder={elem.title_uz}
                      />
                      <input
                        ref={titRu}
                        type="text"
                        placeholder={elem.title_ru}
                      />
                      <input
                        ref={titEn}
                        type="text"
                        placeholder={elem.title_en}
                      />
                      <input
                        ref={decUz}
                        type="text"
                        placeholder={elem.description_uz}
                      />
                      <input
                        ref={decRu}
                        type="text"
                        placeholder={elem.description_ru}
                      />
                      <input
                        ref={decEn}
                        type="text"
                        placeholder={elem.description_en}
                      />
                      <h3>{elem.date}</h3>
                      <input ref={date} type="date" />
                      <button type="submit" className={styled.postBtn}>
                        Edit News
                      </button>
                    </form>
                  </div>
                </>
              ) : null
            )}
          </Box>
        </Modal>
      </div>
    </>
  );
}

export default NewsPut;
