import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const PostNews = createAsyncThunk("News/post", async (body) => {
  return axios.post(`${API_URL}/news`, body).then((res) => res);
});
export const GetNews = createAsyncThunk("News/get", async () => {
  return await axios.get(`${API_URL}/news`).then((response) => response.data);
});
export const DeleteNews = createAsyncThunk("News/delete", async (id) => {
  return await axios
    .delete(`${API_URL}/news/${id}`)
    .then((response) => response.data);
});
export const PutNews = createAsyncThunk("News/put", async ({ body, id }) => {
  return await axios
    .patch(`${API_URL}/news/${id}`, body)
    .then((response) => console.log(response));
});
export const UploadImage = createAsyncThunk("News/upload", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
export const PutUpload = createAsyncThunk("News/uploadPut", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
const NewsSlice = createSlice({
  name: "News",
  initialState: {
    postNews: {
      Error: false,
      Loading: false,
      Success: false,
    },
    getNews: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    deleteNews: {
      Error: false,
      Loading: false,
      Success: false,
    },
    putNews: {
      Error: false,
      Loading: false,
      Success: false,
    },
    uploadNews: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
    uploadPutNews: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
  },
  extraReducers: {
    [PostNews.pending]: (state, action) => {
      state.postNews.Loading = true;
    },
    [PostNews.fulfilled]: (state, action) => {
      state.postNews.Error = false;
      state.postNews.Success = true;
      state.postNews.Loading = false;
    },
    [PostNews.rejected]: (state, action) => {
      state.postNews.Error = true;
      state.postNews.Success = false;
      state.postNews.Loading = false;
    },
    [GetNews.pending]: (state, action) => {
      state.getNews.Loading = true;
    },
    [GetNews.fulfilled]: (state, action) => {
      state.getNews.Error = false;
      state.getNews.Success = true;
      state.getNews.Loading = false;
      state.getNews.Data = action.payload;
    },
    [GetNews.rejected]: (state, action) => {
      state.getNews.Error = true;
      state.getNews.Success = false;
      state.getNews.Loading = false;
      state.getNews.Data = [];
    },
    [DeleteNews.pending]: (state, action) => {
      state.deleteNews.Loading = true;
    },
    [DeleteNews.fulfilled]: (state, action) => {
      state.deleteNews.Error = false;
      state.deleteNews.Success = true;
      state.deleteNews.Loading = false;
    },
    [DeleteNews.rejected]: (state, action) => {
      state.deleteNews.Error = true;
      state.deleteNews.Success = false;
      state.deleteNews.Loading = false;
    },
    [PutNews.pending]: (state, action) => {
      state.putNews.Loading = true;
    },
    [PutNews.fulfilled]: (state, action) => {
      state.putNews.Error = false;
      state.putNews.Success = true;
      state.putNews.Loading = false;
    },
    [PutNews.rejected]: (state, action) => {
      state.putNews.Error = true;
      state.putNews.Success = false;
      state.putNews.Loading = false;
    },
    [UploadImage.pending]: (state, action) => {
      state.uploadNews.Loading = true;
    },
    [UploadImage.fulfilled]: (state, action) => {
      state.uploadNews.Error = false;
      state.uploadNews.Success = true;
      state.uploadNews.Loading = false;
      state.uploadNews.data = action.payload;
    },
    [UploadImage.rejected]: (state, action) => {
      state.uploadNews.Error = true;
      state.uploadNews.Success = false;
      state.uploadNews.Loading = false;
    },
    [PutUpload.pending]: (state, action) => {
      state.uploadPutNews.Loading = true;
    },
    [PutUpload.fulfilled]: (state, action) => {
      state.uploadPutNews.Error = false;
      state.uploadPutNews.Success = true;
      state.uploadPutNews.Loading = false;
      state.uploadPutNews.data = action.payload;
    },
    [PutUpload.rejected]: (state, action) => {
      state.uploadPutNews.Error = true;
      state.uploadPutNews.Success = false;
      state.uploadPutNews.Loading = false;
    },
  },
});

export default NewsSlice.reducer;
