import React, { useState } from "react";
import {
  LogoBlock,
  LoginFormBlock,
  LoginForm,
  Input,
  Label,
  Button,
  Title,
  Img,
} from "../styled-index";
import logo from "../../../assets/logo.png";
import { useDispatch } from "react-redux";
import { SingIn } from "../../../redux/store/login";

function LoginFormCompo() {
  const [phone_number, setnumber] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();
  const HandelClick = (e) => {
    e.preventDefault();
    const userData = {
      email: phone_number,
      password: password,
    };
    dispatch(SingIn(userData));
  };
  return (
    <>
      <LoginFormBlock>
        <LogoBlock>
          <Img width="213px" height="24px" src={logo} alt="INBUSINESS" />
          <Title>Log in</Title>
        </LogoBlock>
        <LoginForm>
          <Label htmlFor="loginEmail">Phone Number</Label>
          <Input
            onChange={(e) => setnumber(e.target.value)}
            id="loginEmail"
            placeholder="email"
            type="email"
          />
          <Label htmlFor="loginPass">Password</Label>
          <Input
            onChange={(e) => setPassword(e.target.value)}
            id="loginPass"
            placeholder="password"
            type="password"
          />
          <Button onClick={HandelClick} type="submit">
            Log in
          </Button>
        </LoginForm>
      </LoginFormBlock>
    </>
  );
}

export default LoginFormCompo;
