import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const PostEvents = createAsyncThunk("Events/post", async (body) => {
  return axios.post(`${API_URL}/events`, body).then((res) => res);
});
export const GetEvents = createAsyncThunk("Events/get", async () => {
  return await axios.get(`${API_URL}/events`).then((response) => response.data);
});
export const DeleteEvents = createAsyncThunk("Events/delete", async (id) => {
  return await axios
    .delete(`${API_URL}/events/${id}`)
    .then((response) => response.data);
});
export const PutEvents = createAsyncThunk(
  "Events/put",
  async ({ body, id }) => {
    return await axios
      .patch(`${API_URL}/events/${id}`, body)
      .then((response) => console.log(response));
  }
);
export const UploadEvents = createAsyncThunk("Events/upload", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
export const PutEventsUpload = createAsyncThunk(
  "Events/uploadPut",
  async (e) => {
    const formData = new FormData();
    formData.append("file", e.target.files[0]);
    formData.append("upload_preset", "br3m8rzo");
    try {
      return await axios
        .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
        .then((response) => response?.data.secure_url);
    } catch (error) {
      return error;
    }
  }
);
const EventsSlice = createSlice({
  name: "Events",
  initialState: {
    postEvents: {
      Error: false,
      Loading: false,
      Success: false,
    },
    getEvents: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    deleteEvents: {
      Error: false,
      Loading: false,
      Success: false,
    },
    putEvents: {
      Error: false,
      Loading: false,
      Success: false,
    },
    uploadEvents: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
    uploadPutEvents: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
  },
  extraReducers: {
    [PostEvents.pending]: (state, action) => {
      state.postEvents.Loading = true;
    },
    [PostEvents.fulfilled]: (state, action) => {
      state.postEvents.Error = false;
      state.postEvents.Success = true;
      state.postEvents.Loading = false;
    },
    [PostEvents.rejected]: (state, action) => {
      state.postEvents.Error = true;
      state.postEvents.Success = false;
      state.postEvents.Loading = false;
    },
    [GetEvents.pending]: (state, action) => {
      state.getEvents.Loading = true;
    },
    [GetEvents.fulfilled]: (state, action) => {
      state.getEvents.Error = false;
      state.getEvents.Success = true;
      state.getEvents.Loading = false;
      state.getEvents.Data = action.payload;
    },
    [GetEvents.rejected]: (state, action) => {
      state.getEvents.Error = true;
      state.getEvents.Success = false;
      state.getEvents.Loading = false;
      state.getEvents.Data = [];
    },
    [DeleteEvents.pending]: (state, action) => {
      state.deleteEvents.Loading = true;
    },
    [DeleteEvents.fulfilled]: (state, action) => {
      state.deleteEvents.Error = false;
      state.deleteEvents.Success = true;
      state.deleteEvents.Loading = false;
    },
    [DeleteEvents.rejected]: (state, action) => {
      state.deleteEvents.Error = true;
      state.deleteEvents.Success = false;
      state.deleteEvents.Loading = false;
    },
    [PutEvents.pending]: (state, action) => {
      state.putEvents.Loading = true;
    },
    [PutEvents.fulfilled]: (state, action) => {
      state.putEvents.Error = false;
      state.putEvents.Success = true;
      state.putEvents.Loading = false;
    },
    [PutEvents.rejected]: (state, action) => {
      state.putEvents.Error = true;
      state.putEvents.Success = false;
      state.putEvents.Loading = false;
    },
    [UploadEvents.pending]: (state, action) => {
      state.uploadEvents.Loading = true;
    },
    [UploadEvents.fulfilled]: (state, action) => {
      state.uploadEvents.Error = false;
      state.uploadEvents.Success = true;
      state.uploadEvents.Loading = false;
      state.uploadEvents.data = action.payload;
    },
    [UploadEvents.rejected]: (state, action) => {
      state.uploadEvents.Error = true;
      state.uploadEvents.Success = false;
      state.uploadEvents.Loading = false;
    },
    [PutEventsUpload.pending]: (state, action) => {
      state.uploadPutEvents.Loading = true;
    },
    [PutEventsUpload.fulfilled]: (state, action) => {
      state.uploadPutEvents.Error = false;
      state.uploadPutEvents.Success = true;
      state.uploadPutEvents.Loading = false;
      state.uploadPutEvents.data = action.payload;
    },
    [PutEventsUpload.rejected]: (state, action) => {
      state.uploadPutEvents.Error = true;
      state.uploadPutEvents.Success = false;
      state.uploadPutEvents.Loading = false;
    },
  },
});

export default EventsSlice.reducer;
