import React from "react";
import { Route, Routes } from "react-router-dom";
import { DataRouter } from "./data-index";
import { Main, WrapperContainer } from "../App-styled";
import Saidbar from "../components/Saidbar";
import Cokies from "universal-cookie";
import Cookies from "universal-cookie/es6";
const cookies = new Cookies();

function AllRouter() {
  const token = cookies.get("autToken");
  return (
    <Main>
      {token ? (
        <>
          <Saidbar />
        </>
      ) : null}
      <WrapperContainer>
        <Routes>
          {DataRouter.map((elem, index) => (
            <Route path={elem.path} key={index} element={elem.Element} />
          ))}
        </Routes>
      </WrapperContainer>
    </Main>
  );
}

export default AllRouter;
