import { useEffect } from "react";
import styled from "./style.module.css";
import { useDispatch, useSelector } from "react-redux";
import { GetCon } from "../../redux/store/connection";
import axios from "axios";

function ConnetctionComponent() {
  const dipatch = useDispatch();
  useEffect(() => {
    dipatch(GetCon());
  }, []);
  const Data = useSelector((state) => state.con.getCon.Data);
  return (
    <>
      <div className={styled.header}>
        <h2>Connection</h2>
      </div>
      <div className={styled.Wrapper}>
        <table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Number</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {Data.map((elem, index) => (
              <tr>
                <td>{elem.name}</td>
                <td>{elem.number}</td>
                <td>
                  <button>Delete</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ConnetctionComponent;
