import React, { useEffect, useState } from "react";
import Header from "../common/headerCommon";
import EventsPost from "./post";
import { useDispatch, useSelector } from "react-redux";
import { Col, Row } from "react-grid-system";
import styled from "./style.module.css";
import { GetEvents } from "../../redux/store/events";
import EventsDelete from "./delete";
import EventsPut from "./put";

function EventsComponent() {
  const [modal, setModal] = useState(false);
  const [delet, setDelete] = useState(false);
  const [put, setPut] = useState(false);
  const [del_id, setDel_id] = useState();
  const [put_id, setPut_id] = useState();

  const HandelOpen = () => {
    setModal(true);
  };
  const HandelClose = () => {
    setModal(false);
  };
  const HandelOpenDel = () => {
    setDelete(true);
  };
  const HandelCloseDel = () => {
    setDelete(false);
  };
  const HandelOpenPut = () => {
    setPut(true);
  };
  const HandelClosePut = () => {
    setPut(false);
  };

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetEvents());
  }, []);

  const Data = useSelector((state) => state.events.getEvents.Data);
  return (
    <>
      <Header title={"Events"} handelOpen={HandelOpen} />
      <EventsPost modal={modal} handleClose={HandelClose} />
      <div className={styled.Wrapper}>
        <Row>
          {Data.map((elem, index) => (
            <Col xs={3} sm={3}>
              <div className={styled.card}>
                <img src={elem.img} alt="photo" />
                <div className={styled.texts}>
                  <h2>{elem.title_uz}</h2>
                  <p>{elem.description_uz}</p>
                  <p>{elem.date}</p>
                </div>
              </div>
              <div className={styled.buttons}>
                <button
                  onClick={() => {
                    setDel_id(elem.id);
                    HandelOpenDel();
                  }}
                  className={styled.delete}
                >
                  Delete
                </button>
                <button
                  onClick={() => {
                    setPut_id(elem.id);
                    HandelOpenPut();
                  }}
                  className={styled.put}
                >
                  Edit
                </button>
              </div>
            </Col>
          ))}
        </Row>
      </div>
      <EventsDelete id={del_id} modal={delet} handleClose={HandelCloseDel} />
      <EventsPut id={put_id} modal={put} handleClose={HandelClosePut} />
    </>
  );
}

export default EventsComponent;
