import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const GetCon = createAsyncThunk("Con/get", async () => {
  return await axios
    .get(`${API_URL}/connection`)
    .then((response) => response.data);
});
export const DeleteCon = createAsyncThunk("Con/delete", async (id) => {
  return await axios
    .delete(`${API_URL}/connection/${id}`)
    .then((response) => response.data);
});

const ConSlice = createSlice({
  name: "Con",
  initialState: {
    getCon: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    deleteCon: {
      Error: false,
      Loading: false,
      Success: false,
    },
  },
  extraReducers: {
    [GetCon.pending]: (state, action) => {
      state.getCon.Loading = true;
    },
    [GetCon.fulfilled]: (state, action) => {
      state.getCon.Error = false;
      state.getCon.Success = true;
      state.getCon.Loading = false;
      state.getCon.Data = action.payload;
    },
    [GetCon.rejected]: (state, action) => {
      state.getCon.Error = true;
      state.getCon.Success = false;
      state.getCon.Loading = false;
      state.getCon.Data = [];
    },
    [DeleteCon.pending]: (state, action) => {
      state.deleteCon.Loading = true;
    },
    [DeleteCon.fulfilled]: (state, action) => {
      state.deleteCon.Error = false;
      state.deleteCon.Success = true;
      state.deleteCon.Loading = false;
    },
    [DeleteCon.rejected]: (state, action) => {
      state.deleteCon.Error = true;
      state.deleteCon.Success = false;
      state.deleteCon.Loading = false;
    },
  },
});

export default ConSlice.reducer;
