import styled from "styled-components";

export const WrapperDiv = styled.div`
  width: 100% !important;
  border: 1px solid black;
  height: 100vh;
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  overflow: hidden;
`;

export const LogoBlock = styled.div`
  width: 100%;
  margin: 20px 0;
`;

export const LoginFormBlock = styled.div`
  width: 50%;
  height: 100%;
  padding: 182px 50px;
  background: #ffffff;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

export const LoginImgBlock = styled.div`
  width: 70%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f1f4fa;
`;

export const LoginForm = styled.form`
  width: 100%;
`;

export const Input = styled.input`
  width: 100%;
  padding: 10px 16px;
  border: 2px solid transparent;
  background: #f1f4fa;
  border-radius: 10px;
  outline: none;
  margin-top: 6px;
  margin-bottom: 10px;
  color: #06152b;
  font-family: "DM Sans", sans-serif;
  :focus {
    border: 2px solid #297d3b;
  }
  ::placeholder {
    color: #06152b;
  }
`;

export const Label = styled.label`
  color: #06152b;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #06152b;
  margin-top: 10px;
  font-family: "DM Sans", sans-serif;
`;

export const Button = styled.button`
  width: 100%;
  margin: 30px 0;
  background: #297d3b;
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
  padding: 10px 16px;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.5s;
  font-family: "DM Sans", sans-serif;
`;

export const Title = styled.h1`
  font-weight: 700;
  font-size: 25.63px;
  line-height: 33px;
  color: #06152b;
  text-align: center;
  font-family: "DM Sans", sans-serif;
`;

export const Img = styled.img`
  margin: 0 auto;
  display: flex;
  align-items: center;
`;
