import { useEffect, useState } from "react";
import Header from "../common/headerCommon";
import styled from "./style.module.css";
import NewsPost from "./newsPost";
import { Col, Row } from "react-grid-system";
import { useDispatch, useSelector } from "react-redux";
import { GetNews } from "../../redux/store/news";
import DeleteNew from "./delete";
import NewsPut from "./put";

function NewsComponent() {
  const [modal, setModal] = useState(false);

  const [delet, setDelete] = useState(false);

  const [put, setPut] = useState(false);

  const [id, setId] = useState();
  const [put_id, setPut_Id] = useState();

  const dispatch = useDispatch();
  const HandelOpen = () => {
    setModal(true);
  };
  const HandelClose = () => {
    setModal(false);
  };
  const HandelOpenDel = () => {
    setDelete(true);
  };
  const HandelCloseDel = () => {
    setDelete(false);
  };
  const HandelOpenPut = () => {
    setPut(true);
  };
  const HandelClosePut = () => {
    setPut(false);
  };
  useEffect(() => {
    dispatch(GetNews());
  }, []);
  const Data = useSelector((state) => state.news.getNews.Data);
  return (
    <>
      <Header title={"News"} handelOpen={HandelOpen} />
      <NewsPost modal={modal} handleClose={HandelClose} />
      <div className={styled.Wrapper}>
        <Row>
          {Data.map((elem, index) => (
            <>
              <Col xs={3} sm={3}>
                <div className={styled.card}>
                  <img src={elem.img} alt="photo" />
                  <div className={styled.texts}>
                    <h2>{elem.title_uz}</h2>
                    <h3>{elem.category_uz}</h3>
                    <p>{elem.description_uz}</p>
                    <p>{elem.date}</p>
                  </div>
                </div>
                <div className={styled.buttons}>
                  <button
                    onClick={() => {
                      setId(elem.id);
                      HandelOpenDel();
                    }}
                    className={styled.delete}
                  >
                    Delete
                  </button>
                  <button
                    onClick={() => {
                      setPut_Id(elem.id);
                      HandelOpenPut();
                    }}
                    className={styled.put}
                  >
                    Edit
                  </button>
                </div>
              </Col>
            </>
          ))}
        </Row>
      </div>
      <DeleteNew id={id} modal={delet} handleClose={HandelCloseDel} />
      <NewsPut id={put_id} modal={put} handleClose={HandelClosePut} />
    </>
  );
}

export default NewsComponent;
