import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import styled from "./style.module.css";
import { useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { GetNews, PostNews, UploadImage } from "../../../redux/store/news";

function NewsPost({ modal, handleClose }) {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };
  const dispatch = useDispatch();
  const inputFileRef = useRef(null);
  const onBtnClick = (e) => {
    e.preventDefault();
    inputFileRef.current.click();
  };
  const onFilechange = (e) => {
    dispatch(UploadImage(e));
  };
  const Loading = useSelector((state) => state.news.uploadNews.Loading);
  const DataImg = useSelector((state) => state.news.uploadNews.data);
  const catUz = useRef();
  const catRU = useRef();
  const catEn = useRef();
  const titUz = useRef();
  const titRu = useRef();
  const titEn = useRef();
  const decUz = useRef();
  const decRu = useRef();
  const decEn = useRef();
  const date = useRef();

  const HandePost = async (e) => {
    e.preventDefault();
    const body = {
      category_uz: catUz.current.value,
      category_ru: catRU.current.value,
      category_en: catEn.current.value,
      title_uz: titUz.current.value,
      title_ru: titRu.current.value,
      title_en: titEn.current.value,
      description_uz: decUz.current.value,
      description_ru: decRu.current.value,
      description_en: decEn.current.value,
      date: date.current.value,
      img: DataImg,
    };
    await dispatch(PostNews(body));
    dispatch(GetNews());
    handleClose();
  };

  return (
    <>
      <div>
        <Modal
          open={modal}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <div className={styled.Wrapper}>
              <form className={styled.form} onSubmit={HandePost}>
                <input
                  type="file"
                  style={{ display: "none" }}
                  ref={inputFileRef}
                  onChange={onFilechange}
                />
                <button onClick={onBtnClick}>
                  {Loading ? <>Loading...</> : <>Add img </>}
                </button>
                <input ref={catUz} type="text" placeholder="category_uz" />
                <input ref={catRU} type="text" placeholder="category_ru" />
                <input ref={catEn} type="text" placeholder="category_en" />
                <input ref={titUz} type="text" placeholder="title_uz" />
                <input ref={titRu} type="text" placeholder="title_ru" />
                <input ref={titEn} type="text" placeholder="title_en" />
                <input ref={decUz} type="text" placeholder="description_uz" />
                <input ref={decRu} type="text" placeholder="description_ru" />
                <input ref={decEn} type="text" placeholder="description_en" />
                <input ref={date} type="date" placeholder="date" />
                <button type="submit" className={styled.postBtn}>
                  Add News
                </button>
              </form>
            </div>
          </Box>
        </Modal>
      </div>
    </>
  );
}

export default NewsPost;
