import React from "react";
import {
    LoginImgBlock,
    Img,
  } from "../styled-index";

import loginImg from '../../../assets/loginimg.png'

function LoginImgCompo() {
  return (
    <>
      <LoginImgBlock>
        <Img width="547px" height="504px" src={loginImg} alt="image" />
      </LoginImgBlock>
    </>
  );
}

export default LoginImgCompo;
