import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { API_URL } from "../../servies";

export const PostDec = createAsyncThunk("Dec/post", async (body) => {
  return axios.post(`${API_URL}/description`, body).then((res) => res);
});
export const GetDec = createAsyncThunk("Dec/get", async () => {
  return await axios
    .get(`${API_URL}/description`)
    .then((response) => response.data);
});
export const DeleteDec = createAsyncThunk("Dec/delete", async (id) => {
  return await axios
    .delete(`${API_URL}/description/${id}`)
    .then((response) => response.data);
});
export const PutDec = createAsyncThunk("Dec/put", async ({ body, id }) => {
  return await axios
    .patch(`${API_URL}/description/${id}`, body)
    .then((response) => console.log(response));
});
export const UploadDec = createAsyncThunk("Dec/upload", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
export const PutDecUpload = createAsyncThunk("Dec/uploadPut", async (e) => {
  const formData = new FormData();
  formData.append("file", e.target.files[0]);
  formData.append("upload_preset", "br3m8rzo");
  try {
    return await axios
      .post("https://api.cloudinary.com/v1_1/dibvlfm8v/upload", formData)
      .then((response) => response?.data.secure_url);
  } catch (error) {
    return error;
  }
});
const DecSlice = createSlice({
  name: "Dec",
  initialState: {
    postDec: {
      Error: false,
      Loading: false,
      Success: false,
    },
    getDec: {
      Error: false,
      Loading: false,
      Success: false,
      Data: [],
    },
    deleteDec: {
      Error: false,
      Loading: false,
      Success: false,
    },
    putDec: {
      Error: false,
      Loading: false,
      Success: false,
    },
    uploadDec: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
    uploadPutDec: {
      Error: false,
      Loading: false,
      Success: false,
      data: "",
    },
  },
  extraReducers: {
    [PostDec.pending]: (state, action) => {
      state.postDec.Loading = true;
    },
    [PostDec.fulfilled]: (state, action) => {
      state.postDec.Error = false;
      state.postDec.Success = true;
      state.postDec.Loading = false;
    },
    [PostDec.rejected]: (state, action) => {
      state.postDec.Error = true;
      state.postDec.Success = false;
      state.postDec.Loading = false;
    },
    [GetDec.pending]: (state, action) => {
      state.getDec.Loading = true;
    },
    [GetDec.fulfilled]: (state, action) => {
      state.getDec.Error = false;
      state.getDec.Success = true;
      state.getDec.Loading = false;
      state.getDec.Data = action.payload;
    },
    [GetDec.rejected]: (state, action) => {
      state.getDec.Error = true;
      state.getDec.Success = false;
      state.getDec.Loading = false;
      state.getDec.Data = [];
    },
    [DeleteDec.pending]: (state, action) => {
      state.deleteDec.Loading = true;
    },
    [DeleteDec.fulfilled]: (state, action) => {
      state.deleteDec.Error = false;
      state.deleteDec.Success = true;
      state.deleteDec.Loading = false;
    },
    [DeleteDec.rejected]: (state, action) => {
      state.deleteDec.Error = true;
      state.deleteDec.Success = false;
      state.deleteDec.Loading = false;
    },
    [PutDec.pending]: (state, action) => {
      state.putDec.Loading = true;
    },
    [PutDec.fulfilled]: (state, action) => {
      state.putDec.Error = false;
      state.putDec.Success = true;
      state.putDec.Loading = false;
    },
    [PutDec.rejected]: (state, action) => {
      state.putDec.Error = true;
      state.putDec.Success = false;
      state.putDec.Loading = false;
    },
    [UploadDec.pending]: (state, action) => {
      state.uploadDec.Loading = true;
    },
    [UploadDec.fulfilled]: (state, action) => {
      state.uploadDec.Error = false;
      state.uploadDec.Success = true;
      state.uploadDec.Loading = false;
      state.uploadDec.data = action.payload;
    },
    [UploadDec.rejected]: (state, action) => {
      state.uploadDec.Error = true;
      state.uploadDec.Success = false;
      state.uploadDec.Loading = false;
    },
    [PutDecUpload.pending]: (state, action) => {
      state.uploadPutDec.Loading = true;
    },
    [PutDecUpload.fulfilled]: (state, action) => {
      state.uploadPutDec.Error = false;
      state.uploadPutDec.Success = true;
      state.uploadPutDec.Loading = false;
      state.uploadPutDec.data = action.payload;
    },
    [PutDecUpload.rejected]: (state, action) => {
      state.uploadPutDec.Error = true;
      state.uploadPutDec.Success = false;
      state.uploadPutDec.Loading = false;
    },
  },
});

export default DecSlice.reducer;
